<template>
  <div class="division-game">
    <div class="game-header">
      <h2>‚ûó Èô§Ê≥ïËææ‰∫∫</h2>
      <div class="game-stats">
        <span class="score">ÂæóÂàÜ: {{ score }}</span>
        <span class="time">Êó∂Èó¥: {{ timeLeft }}s</span>
        <span class="level">Á≠âÁ∫ß: {{ level }}</span>
      </div>
    </div>

    <div v-if="!gameStarted" class="game-start">
      <div class="start-content">
        <h3>ÂáÜÂ§áÂºÄÂßãÈô§Ê≥ïÊåëÊàòÔºÅ</h3>
        <p>ÁêÜËß£Èô§Ê≥ïÊ¶ÇÂøµÔºåÂ≠¶‰ºöÊï¥Èô§Âíå‰ΩôÊï∞</p>
        <div class="difficulty-select">
          <label>ÈÄâÊã©ÈöæÂ∫¶:</label>
          <select v-model="selectedDifficulty">
            <option value="easy">ÁÆÄÂçï (Êï¥Èô§)</option>
            <option value="medium">‰∏≠Á≠â (Êúâ‰ΩôÊï∞)</option>
            <option value="hard">Âõ∞Èöæ (Ê∑∑Âêà)</option>
          </select>
        </div>
        <button class="btn btn-primary" @click="startGame">ÂºÄÂßãÊ∏∏Êàè</button>
      </div>
    </div>

    <div v-else-if="!gameOver" class="game-play">
      <div class="question-container">
        <div class="question">{{ dividend }} √∑ {{ divisor }} = ?</div>
        <div class="answer-input">
          <div class="input-group">
            <label>ÂïÜ:</label>
            <input 
              v-model="quotient" 
              type="number" 
              placeholder="ËæìÂÖ•ÂïÜ"
              @keyup.enter="checkAnswer"
            />
          </div>
          <div class="input-group" v-if="selectedDifficulty !== 'easy'">
            <label>‰ΩôÊï∞:</label>
            <input 
              v-model="remainder" 
              type="number" 
              placeholder="ËæìÂÖ•‰ΩôÊï∞"
              @keyup.enter="checkAnswer"
            />
          </div>
          <button class="btn btn-primary" @click="checkAnswer">Á°ÆËÆ§</button>
        </div>
      </div>

      <div class="progress-bar">
        <div class="progress" :style="{ width: progressPercent + '%' }"></div>
      </div>

      <div class="feedback" v-if="showFeedback">
        <div :class="['feedback-message', isCorrect ? 'correct' : 'incorrect']">
          {{ feedbackMessage }}
        </div>
      </div>

      <div class="hint" v-if="showHint">
        <p>üí° ÊèêÁ§∫: {{ hintMessage }}</p>
      </div>
    </div>

    <div v-else class="game-over">
      <div class="game-over-content">
        <h3>Ê∏∏ÊàèÁªìÊùüÔºÅ</h3>
        <div class="final-stats">
          <p>ÊúÄÁªàÂæóÂàÜ: <span class="final-score">{{ score }}</span></p>
          <p>Ê≠£Á°ÆÈ¢òÁõÆ: {{ correctAnswers }}/{{ totalQuestions }}</p>
          <p>Áî®Êó∂: {{ totalTime }}Áßí</p>
        </div>
        <div class="game-actions">
          <button class="btn btn-primary" @click="restartGame">ÂÜçÁé©‰∏ÄÊ¨°</button>
          <button class="btn btn-secondary" @click="goBack">ËøîÂõûÊ∏∏ÊàèÂàóË°®</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'
import { gameStorage } from '@/services/gameStorage'
import { achievementService } from '@/services/achievementService'

const router = useRouter()

// Ê∏∏ÊàèÁä∂ÊÄÅ
const gameStarted = ref(false)
const gameOver = ref(false)
const selectedDifficulty = ref('medium')
const score = ref(0)
const timeLeft = ref(60)
const level = ref(1)
const correctAnswers = ref(0)
const totalQuestions = ref(0)
const totalTime = ref(0)

// Ê∏∏ÊàèÈ¢òÁõÆ
const dividend = ref(0)
const divisor = ref(0)
const quotient = ref('')
const remainder = ref('')
const showFeedback = ref(false)
const isCorrect = ref(false)
const feedbackMessage = ref('')
const showHint = ref(false)
const hintMessage = ref('')

// ÂÆöÊó∂Âô®
let gameTimer: NodeJS.Timeout | null = null
let feedbackTimer: NodeJS.Timeout | null = null

// ÈöæÂ∫¶ÈÖçÁΩÆ
const difficultyConfig = {
  easy: { time: 60, maxDividend: 50, maxDivisor: 10 },
  medium: { time: 60, maxDividend: 100, maxDivisor: 15 },
  hard: { time: 60, maxDividend: 200, maxDivisor: 20 }
}

// ËÆ°ÁÆóËøõÂ∫¶ÁôæÂàÜÊØî
const progressPercent = computed(() => {
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  return ((config.time - timeLeft.value) / config.time) * 100
})

// ÁîüÊàêÊñ∞È¢òÁõÆ
const generateQuestion = () => {
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  
  if (selectedDifficulty.value === 'easy') {
    // ÁÆÄÂçïÊ®°ÂºèÔºöÂè™ÁîüÊàêÊï¥Èô§ÁöÑÈ¢òÁõÆ
    divisor.value = Math.floor(Math.random() * (config.maxDivisor - 1)) + 2
    const tempQuotient = Math.floor(Math.random() * 10) + 1
    dividend.value = divisor.value * tempQuotient
    hintMessage.value = 'Ëøô‰∏™È¢òÁõÆÂèØ‰ª•Êï¥Èô§ÔºåÊ≤°Êúâ‰ΩôÊï∞'
  } else {
    // ‰∏≠Á≠âÂíåÂõ∞ÈöæÊ®°ÂºèÔºöÁîüÊàêÊúâ‰ΩôÊï∞ÁöÑÈ¢òÁõÆ
    divisor.value = Math.floor(Math.random() * (config.maxDivisor - 1)) + 2
    const tempQuotient = Math.floor(Math.random() * 10) + 1
    const tempRemainder = Math.floor(Math.random() * (divisor.value - 1)) + 1
    dividend.value = divisor.value * tempQuotient + tempRemainder
    hintMessage.value = `Ëøô‰∏™È¢òÁõÆÊúâ‰ΩôÊï∞Ôºå‰ΩôÊï∞Â∞è‰∫é ${divisor.value}`
  }
  
  quotient.value = ''
  remainder.value = ''
  totalQuestions.value++
  
  // ÊòæÁ§∫ÊèêÁ§∫
  showHint.value = true
  setTimeout(() => {
    showHint.value = false
  }, 4000)
}

// Ê£ÄÊü•Á≠îÊ°à
const checkAnswer = () => {
  // Á°Æ‰øù quotient ÊòØÊúâÊïàÁöÑÂ≠óÁ¨¶‰∏≤‰∏î‰∏ç‰∏∫Á©∫
  const quotientStr = quotient.value?.toString()?.trim()
  if (!quotientStr) return
  
  const userQuotient = parseInt(quotientStr)
  const userRemainder = parseInt(remainder.value?.toString() || '0')
  const correctQuotient = Math.floor(dividend.value / divisor.value)
  const correctRemainder = dividend.value % divisor.value
  
  // Ê£ÄÊü•ÊòØÂê¶‰∏∫ÊúâÊïàÊï∞Â≠ó
  if (isNaN(userQuotient)) return
  
  let isAnswerCorrect = false
  
  if (selectedDifficulty.value === 'easy') {
    // ÁÆÄÂçïÊ®°ÂºèÔºöÂè™Ê£ÄÊü•ÂïÜÔºå‰∏îÂøÖÈ°ªÊï¥Èô§
    isAnswerCorrect = userQuotient === correctQuotient && correctRemainder === 0
  } else {
    // ‰∏≠Á≠âÂíåÂõ∞ÈöæÊ®°ÂºèÔºöÊ£ÄÊü•ÂïÜÂíå‰ΩôÊï∞
    isAnswerCorrect = userQuotient === correctQuotient && userRemainder === correctRemainder
  }
  
  if (isAnswerCorrect) {
    isCorrect.value = true
    feedbackMessage.value = '‚úÖ Ê≠£Á°ÆÔºÅ'
    score.value += 10 * level.value
    correctAnswers.value++
    
    // ÊØèÁ≠îÂØπ5È¢òÂçá‰∏ÄÁ∫ß
    if (correctAnswers.value % 5 === 0) {
      level.value++
      feedbackMessage.value += ` ÂçáÁ∫ßÂà∞ ${level.value} Á∫ßÔºÅ`
    }
  } else {
    isCorrect.value = false
    if (selectedDifficulty.value === 'easy') {
      feedbackMessage.value = `‚ùå ÈîôËØØÔºÅÊ≠£Á°ÆÁ≠îÊ°àÊòØ ${correctQuotient}`
    } else {
      feedbackMessage.value = `‚ùå ÈîôËØØÔºÅÊ≠£Á°ÆÁ≠îÊ°àÊòØ ÂïÜ: ${correctQuotient}, ‰ΩôÊï∞: ${correctRemainder}`
    }
    score.value = Math.max(0, score.value - 5)
  }
  
  showFeedback.value = true
  
  // Ê∏ÖÈô§ÂèçÈ¶àÊèêÁ§∫
  if (feedbackTimer) clearTimeout(feedbackTimer)
  feedbackTimer = setTimeout(() => {
    showFeedback.value = false
    generateQuestion()
  }, 2000)
}

// ÂºÄÂßãÊ∏∏Êàè
const startGame = () => {
  gameStarted.value = true
  gameOver.value = false
  score.value = 0
  level.value = 1
  correctAnswers.value = 0
  totalQuestions.value = 0
  totalTime.value = 0
  
  const config = difficultyConfig[selectedDifficulty.value as keyof typeof difficultyConfig]
  timeLeft.value = config.time
  
  generateQuestion()
  startTimer()
}

// ÂºÄÂßãËÆ°Êó∂Âô®
const startTimer = () => {
  gameTimer = setInterval(() => {
    timeLeft.value--
    totalTime.value++
    
    if (timeLeft.value <= 0) {
      endGame()
    }
  }, 1000)
}

// ÁªìÊùüÊ∏∏Êàè
const endGame = () => {
  if (gameTimer) {
    clearInterval(gameTimer)
    gameTimer = null
  }
  gameOver.value = true
  
  // ‰øùÂ≠òÊ∏∏ÊàèËÆ∞ÂΩï
  gameStorage.addGameRecord({
    gameType: 'division',
    gameName: 'Èô§Ê≥ïËææ‰∫∫',
    score: score.value,
    level: level.value,
    correctAnswers: correctAnswers.value,
    totalQuestions: totalQuestions.value,
    totalTime: totalTime.value,
    difficulty: selectedDifficulty.value
  })

  // Ê£ÄÊü•ÊàêÂ∞±
  achievementService.checkAchievements()
}

// ÈáçÊñ∞ÂºÄÂßãÊ∏∏Êàè
const restartGame = () => {
  gameStarted.value = false
  gameOver.value = false
  quotient.value = ''
  remainder.value = ''
  showFeedback.value = false
  showHint.value = false
}

// ËøîÂõûÊ∏∏ÊàèÂàóË°®
const goBack = () => {
  router.push('/games')
}

// ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ËÅöÁÑ¶ËæìÂÖ•Ê°Ü
onMounted(() => {
  if (gameStarted.value && !gameOver.value) {
    setTimeout(() => {
      const input = document.querySelector('input') as HTMLInputElement
      if (input) input.focus()
    }, 100)
  }
})

// ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÁêÜÂÆöÊó∂Âô®
onUnmounted(() => {
  if (gameTimer) clearInterval(gameTimer)
  if (feedbackTimer) clearTimeout(feedbackTimer)
})
</script>

